@font-face {
	font-family: 'Montserrat-Thin';
	src: url('./assets/Fonts/Montserrat-Thin.ttf') format('truetype'); // font-weight: 100;
}
@font-face {
	font-family: 'Montserrat-Regular';
	src: url('./assets/Fonts/Montserrat-Regular.ttf') format('truetype'); // font-weight:400
}
@font-face {
	font-family: 'Montserrat-Medium';
	src: url('./assets/Fonts/Montserrat-Medium.ttf') format('truetype'); // font-weight: 500;
}
@font-face {
	font-family: 'Montserrat-SemiBold';
	src: url('./assets/Fonts/Montserrat-SemiBold.ttf') format('truetype'); // font-weight: 600;
}
@font-face {
	font-family: 'Montserrat-Bold';
	src: url('./assets/Fonts/Montserrat-Bold.ttf') format('truetype'); // font-weight: 700;
}
@font-face {
	font-family: 'Montserrat-ExtraBold';
	src: url('./assets/Fonts/Montserrat-ExtraBold.ttf') format('truetype'); // font-weight: 800;
}

*,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 1);
	box-shadow: inset 0 0 6px rgba(0, 0, 0, 1);
	background-color: #f5f5f5;
}

::-webkit-scrollbar {
	width: 6px;
	background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
	background-color: #000000;
}

#root {
	background-color: rgba(0, 0, 0, 1);
	overflow-x: hidden;
	color: #fff;
}
body {
	font-family: 'Montserrat-Regular';
	font-style: normal;
	font-weight: 400;
}
.custom-container {
	max-width: 1440px;
	margin: 0 9.6vw;
}

@media screen and (min-width: 1920px) {
	.custom-container {
		margin: 0 auto;
	}
}

.section {
	&__title {
		font-weight: 700;
		font-size: 48px;
		line-height: 140%;
		color: #ffffff;
	}
}
.mt {
	margin-top: 100px;
}
// mobile
@media screen and (max-width: 768px) {
	.custom-container {
		margin: 0 20px;
	}
	.section-header__titles {
		margin-bottom: 40px;
	}

	.registration-section {
		.question-cards {
			grid-template-columns: 1fr !important;
			svg {
				display: none;
			}
		}
	}
	.question-section__link a {
		font-size: 24px !important;
	}
	.accordion {
		.accordion-title {
			font-size: 24px !important;
		}
		.accordion-content {
			font-size: 18px !important;
		}
	}
	footer .footer-main {
		flex-direction: column;
		text-align: center;
		button {
			width: 100%;
			justify-content: center !important;
			font-size: 20px;
		}
	}
	.footer-bottom {
		text-align: center;
	}
}
